.container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: linear-gradient(#232c4f, #460d0d);
  min-width: 750px;
  min-height: 860px;
}
.title {
  font-size: 85px;
  color: white;
  animation: girarTitulo 1s;
}

@keyframes girarTitulo {
  from {
    transform: rotateX(90deg);
  }
  to {
    transform: rotateX(0deg);
  }
}

.containerInput {
  background-color: rgba(255, 255, 255, 0.2);
  padding: 15px;
  margin: 34px 0;
  display: flex;
  border-radius: 10px;
  box-shadow: 1px 3px 8px rgba(0, 0, 0, 0.5);
}

.containerInput input {
  background-color: transparent;
  border: 0;
  font-size: 35px;
  color: white;
  margin-right: 10px;
}

.containerInput input:focus {
  outline: 0;
}

.containerInput input::placeholder {
  color: #f1f1f1;
  opacity: 0.4;
}

.buttonSearch {
  background-color: transparent;
  border: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: transform 0.5s;
}

.buttonSearch:hover {
  transform: scale(1.4);
}

.main {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: white;
  width: 450px;
  border-radius: 10px;
}

.main h2 {
  margin: 16px 0;
  font-size: 39px;
}

.main span {
  margin-bottom: 16px;
  font-weight: bold;
}

@media (max-width: 1050px) {
  .title {
    font-size: 400%;
  }
  .main h2 {
    font-size: 200%;
  }
  .main {
    width: 50%;
  }
  .containerInput input {
    font-size: 150%;
  }
}
